:root {
    --primary-color: #34cb79;
    --title-color: #322153;
    --text-color: #6c6c80;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html,
body {
    margin: 0;
    padding: 0;
    height: 100%;
}

body {
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
    color: var(--text-color);

    overflow-x: hidden;
}

body,
input,
button {
    font-family: Roboto, Arial, Helvetica, sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: var(--title-color);
    font-family: Ubuntu;
}
